
.textHeader {
    font-family: "Montserrat";
    font-size: 16px;
    font-weight: 600;
    color: #005475;
    border-bottom: 3px solid #005475BF;
    text-align: center;
}

.textBody {
    font-family: "Montserrat";
    font-size: 16px;
    font-weight: 600;
    color:#333333BF;
    text-align: center;

}
@keyframes slideUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
.modal {
    animation-name: slideUp;
    animation-duration: 0.5s; 
    animation: slideUp 0.5s ease-out; /* Анимация всплытия */
  }